input[type="range"] {
      height: 2px;
      -webkit-appearance: none;
      margin: 10px 0;
      width: 44%;
}
input[type="range"]:first-of-type {
      margin: 10px 24px 10px 0;
}
input[type="range"]:focus {
      outline: none;
      border: none;
}
input[type="range"]::-webkit-slider-runnable-track {
      width: 100%;
      height: 3px;
      cursor: pointer;
      background: linear-gradient(90deg, #f9b702 80%, #ff4463);
      border-radius: 5px;
      border: 2px solid #ffffff;
}
input[type="range"]::-webkit-slider-thumb {
      border: 2px solid #ffffff;
      height: 3px;
      width: 38px;
      border-radius: 15px;
      background: #f9b702;
      cursor: pointer;
      -webkit-appearance: none;
      margin-top: -3px;
}
input[type="range"]:focus::-webkit-slider-runnable-track {
      background: #f9b702;
}
input[type="range"]::-moz-range-track {
      width: 100%;
      height: 3px;
      cursor: pointer;
      background: linear-gradient(90deg, #f9b702 80%, #ff4463);
}
input[type="range"]::-moz-range-thumb {
      border: 2px solid #ffffff;
      height: 24px;
      width: 24px;
      border-radius: 24px;
      background: #f9b702;
      cursor: pointer;
}
input[type="range"]::-ms-track {
      width: 100%;
      height: 3px;
      cursor: pointer;
      background: #f9b702;
      border-color: transparent;
      color: transparent;
}
input[type="range"]::-ms-fill-lower {
      background: #f9b702;
      border: 2px solid #ffffff;
      border-radius: 15px;
}
input[type="range"]::-ms-fill-upper {
      background: #f9b702;
      border: 2px solid #ffffff;
      border-radius: 15px;
}
input[type="range"]::-ms-thumb {
      margin-top: 1px;
      border: 2px solid #ffffff;
      height: 20px;
      width: 38px;
      border-radius: 15px;
      background: #f9b702;
      cursor: pointer;
}
input[type="range"]:focus::-ms-fill-lower {
      background: #f9b702;
}
input[type="range"]:focus::-ms-fill-upper {
      background: #f9b702;
}

