}

.dp-day-disabled {
      background: transparent;
      text-decoration: line-through;

      color: #969699;
}

.dp-day-disabled:focus,
.dp-day-disabled:hover {
      background: #ddd;
}

.dp-focuser {
      position: absolute;
      z-index: 0;
      top: 50%;
      left: 50%;
}

